
.OrientationGraphic {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0px 0px 20px 1px #deefff;
}

.OrientationGraphic:before, .OrientationGraphic:after  {
  content: "";
  display: block;
  position: absolute;
  background-color: #deefff;
  z-index: 0;
}

.OrientationGraphic:before {
  width: 100%;
  height: 1px;
}

.OrientationGraphic:after {
  height: 100%;
  width: 1px;
}

.OrientationGraphic svg {
  width: 180px;
  z-index: 10;
}

.OrientationGraphic path {
  fill: transparent;
  stroke: #00d9fe66;
  stroke-width: 1px;
  transition: fill 200ms linear;
  cursor: pointer;
}

.OrientationGraphic path:hover {
  fill: #88eeff;
}
