
.Selector.open .dropdown-menu {
  display: block;
}

.Selector .selector-title {
  cursor: pointer;
  white-space: nowrap;
  padding: 6px 8px;
  background: #fff;
  font-weight: 400;
  color: #333;
  border-radius: 3px;
  border: 1px solid #888;
  line-height: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Selector .selector-title:hover {
  border-color: #007bff;
}

.Selector .selector-title i {
  font-size: 12px;
  margin-left: 8px;
}

.Selector .selector-menu {
  display: none;
  background: #fff;
  margin-top: 3px;
  border-radius: 3px;
  box-shadow: 0px 1px 3px -1px #888;
  padding: 4px 0px;
}

.Selector .selector-menu-item {
  padding: 4px 8px;
  cursor: pointer;
  font-size: 12px;
  color: #333;
}

.Selector .selector-menu-item.selected {
  background: #eee;
  cursor: default;
}

.Selector .selector-menu-item.selected:hover {
  background: #eee;
  color: #333;
}

.Selector .selector-menu-item:hover {
  background: #007bff;
  color: #fff;
}

.Selector.open .selector-menu {
  display: block;
}
