
.Mock {
  height: 100%;
  width: 100%;
  display: block;
  position: relative;
}

.Mock .anchor {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.Mock.north .anchor {
  top: auto;
  bottom: 50%;
  transform: translateX(-50%);
}

.Mock.north-east .anchor {
  top: auto;
  left: 50%;
  bottom: 50%;
  transform: none;
}

.Mock.east .anchor {
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
}

.Mock.south-east .anchor {
  top: 50%;
  left: 50%;
  transform: none;
}

.Mock.south .anchor {
  top: 50%;
  bottom: auto;
  transform: translateX(-50%);
}

.Mock.south-west .anchor {
  top: 50%;
  left: auto;
  right: 50%;
  transform: none;
}

.Mock.west .anchor {
  top: 50%;
  left: auto;
  right: 50%;
  transform: translateY(-50%);
}

.Mock.north-west .anchor {
  top: auto;
  left: auto;
  right: 50%;
  bottom: 50%;
  transform: none;
}

.Element {
  all: initial;
  display: block;
}
