
.ContextBuilder {
  height: 100%;
  width: 100%;
}

.context-cta {
  width: 100%;
  margin-bottom: 30px;
  font-size: 14px;
  font-style: italic;
  color: #6f6f6f;
}

.context-menu {
  margin-bottom: 10px;
}

.ctx {
  display: flex;
  justify-content: space-between;
  padding: 8px 10px 8px 15px;
  user-select: none;
  cursor: grab;
  font-size: 14px;
  border: 1px solid #d0d0d0;
  background: #fcfcfc;
  border-radius: 2px;
}

.ctx-input {
  font-size: 14px;
  background: transparent;
  border: none;
  line-height: 1;
  margin-right: 16px;
  cursor: grab;
}

.ctx-input:focus {
  cursor: text;
  color: #007bff;
}

.ctx-remove {
  line-height: 17px;
  padding: 0px 5px;
  cursor: pointer;
  color: #ff4444;
  opacity: 0;
  transition: opacity 200ms linear;
}

.ctx:hover .ctx-remove {
  opacity: 1;
}

.add-context {
  padding: 5px 12px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
}

.add-context:hover, .add-context:focus {
  background-color: #0062cc;
  border-color: #0062cc;
}

.ContextBuilder .divider {
  height: 10px;
  position: relative;
}

.ContextBuilder .divider:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0px;
  left: 0px;
  border-bottom: 1px solid #007bff;
  opacity: 0;
}

.ContextBuilder .divider.dragged:before {
  opacity: 1;
}

.ContextBuilder .ctx-wrapper-title {
  margin-bottom: 10px;
  position: relative;
}

.ContextBuilder .ctx-wrapper-input {
  display: block;
  width: 100%;
  font-size: 14px;
  font-weight: 600;
  background: transparent;
  border: none;
}

.ContextBuilder .ctx-wrapper-input:focus {
  color: #007bff;
}

.ContextBuilder .remove-dim {
  position: absolute;
  top: 0px;
  right: 0px;
  color: #ff4444;
  cursor: pointer;
  font-size: 10px;
  line-height: 16px;
  text-align: center;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transition: opacity 200ms linear;
  opacity: 0;
}

.ContextBuilder .ctx-wrapper-title:hover .remove-dim {
  opacity: 1;
}

.ContextBuilder .remove-dim:hover {
  background-color: #ff4444;
  color: #fff;
}

.ContextBuilder .dimension-menu {
  display: flex;
}

.ContextBuilder .dimension-menu-item {
  margin-right: 30px;
}

.ContextBuilder .add-dimension {
  display: block;
  padding: 8px 15px;
  border: 2px solid #007bff;
  background: #aacdff;
  color: #005899;
  border-radius: 4px;
  font-size: 12px;
}

.ContextBuilder .add-dimension i {
  margin-left: 6px;
}
