
.StyleEditor {
  display: flex;
  flex-wrap: wrap;
  z-index: 0;
  position: relative;
  border: 1px dotted transparent;
  border-radius: 2px;
}

.StyleEditor.open {
  border-color: #ddd;
}

.StyleEditor.delete {
  border-color: #ff4444;
}

.StyleEditor.hidden .style-input input {
  text-decoration: line-through;
}

.StyleEditor:focus-within {
  z-index: 100;
}

.style-input {
  flex: 1;
  position: relative;
}

.style-input input {
  width: 100%;
  display: block;
  border: none;
  padding: 4px;
  text-overflow: ellipsis;
}

.style-input input:focus {
  color: #007bff;
}

.dom-value input {
  text-align: right;
}

.dom-key input.focused + .key-dropdown {
  height: auto;
  visibility: visible;
}

.key-dropdown {
  height: 0px;
  visibility: hidden;
  position: absolute;
  display: block;
  padding: 5px 0px;
  background: #fff;
  border: 1px solid #aaa;
  border-radius: 5px;
  box-shadow: 0px 1px 3px -1px #aaa;
  max-height: 150px;
  overflow-x: hidden;
}

.key-item {
  font-size: 11px;
  padding: 4px 8px;
  cursor: pointer;
  color: #555;
}

.key-item:hover {
  background-color: #007bff;
  color: #fff;
}

.style-icon {
  font-size: 12px;
  text-align: center;
  width: 22px;
  height: 22px;
  line-height: 22px;
  color: #007bff;
  cursor: pointer;
}

.style-header-list {
  height: 0px;
  overflow: hidden;
  transition: height 200ms linear;
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
}

.StyleEditor.open .style-header-list {
  height: 26px;
}

.style-header-item {
  padding: 4px;
  font-size: 10px;
  cursor: pointer;
}

.style-header-icon {
  display: block;
  background-color: #ddd;
  color: #333;
  border-radius: 50%;
  line-height: 18px;
  text-align: center;
  width: 18px;
  height: 18px;
}

.style-header-item:hover .style-header-icon {
  background-color: #007bff;
  color: #fff;
}
