
.Canvas {
  background-color: #fff;
  flex: 1;
}

.Canvas .highlighter {
  opacity: 0.8;
  position: fixed;
  border-style: solid;
  border-color: transparent;
  background-color: #fcdc9e;
  border-width: 0px;
}

.Canvas .highlighter > * {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.Canvas .highlighter .highlight-margin-box {
  background-color: #f8cca1;
}

.Canvas .highlighter .highlight-padding-box {
  background-color: #c3cf8e;
}

.Canvas .highlighter .highlight-content-box {
  background-clip: content-box;
  background-color: #8db6c1;
}
