
.Modal {
  display: none;
  position: fixed;
  z-index: 1000;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.75);
  align-items: center;
  justify-content: center;
}

.Modal.open {
  display: flex;
}

.Modal .modal-content {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 1px 5px -1px #000;
}
