

.selector-title {
  padding: 6px 10px;
  background-color: #fcfcfc;
  border: 1px solid #88adff;
  border-radius: 2px;
  font-weight: normal;
  font-size: 12px;
  line-height: 1;
  cursor: pointer;
  min-width: 85px;
}

.selector-title:hover {
  color: #007bff;
}

.component-item > .selector-title:hover {
  color: #55bb55;
}

.selector-title.targeted {
  background-color: #0058cc;
  box-shadow: inset 0px 0px 3px 1px #002488;
  color: #fff !important;
}

.selector-item.selected > .selector-title {
  border-color: #007bff;
  background-color: #007bff;
  color: #fff;
}


.selector-item > .selector-item {
  padding-left: 20px;
}

.selector-item > .selector-item > .selector-title {
  margin: 5px 0px;
}

.selector-item.component-item > .selector-title {
  border-color: #88dd88;
}

.selector-item.selected.component-item > .selector-title {
  border-color: #55bb55;
  background-color: #55bb55;
}
