
.EditingCanvas {
  overflow: hidden;
  background-color: #fff;
  flex: 1;
  position: relative;
}

.EditingCanvas .highlighter {
  opacity: 0.8;
  position: fixed;
  border-style: solid;
  border-color: transparent;
  background-color: #fcdc9e;
  border-width: 0px;
}

.EditingCanvas .highlighter > * {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.EditingCanvas .highlighter .highlight-margin-box {
  background-color: #f8cca1;
}

.EditingCanvas .highlighter .highlight-padding-box {
  background-color: #c3cf8e;
}

.EditingCanvas .highlighter .highlight-content-box {
  background-clip: content-box;
  background-color: #8db6c1;
}

.EditingCanvas .top-drawer {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 5px;
  min-width: 80px;
  background-color: #f8f8f8;
  box-shadow: 0px 1px 3px -1px #888;
}

.EditingCanvas .top-drawer-button {
  background: none;
  border: none;
  font-size: 18px;
  color: #007bff;
  border-radius: 50%;
  display: block;
  height: 18px;
  width: 18px;
  padding: 0px;
}
