
.Preview {
  display: flex;
  height: 100%;
}

.side-panel, .main-panel {
  height: 100%;
}

.side-panel {
  margin-right: 20px;
  min-width: 180px;
}

.main-panel {
  flex: 1;
}

.state-view-selector {
  display: flex;
  align-items: center;
  font-size: 14px;
  margin-bottom: 14px;
}

.state-view-menu {
  display: flex;
  margin: 0px 8px;
}

.state-view-item {
  margin: 0px 8px;
  cursor: pointer;
}

.state-view-item:hover {
  color: #4da3ff;
}

.state-view-item.active {
  color: #007bff;
  text-decoration: underline;
}

.Preview .Canvas {
  width: 100%;
  height: 100%;
}

.Preview .state-view-dir {
  padding: 4px 8px;
  cursor: pointer;
}

.Preview .state-view-dir:hover {
  color: #007bff;
}

.Preview .state-view-dir.disabled {
  pointer-events: none;
  opacity: 0.5;
}

.Preview .dimension-dropdown.open .dropdown-menu {
  display: block;
}

.Preview .dimension-dropdown-title {
  cursor: pointer;
  white-space: nowrap;
  padding: 6px 8px;
  background: #fff;
  font-weight: 400;
  color: #333;
  border-radius: 3px;
  border: 1px solid #888;
  line-height: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Preview .dimension-dropdown-title:hover {
  border-color: #007bff;
}

.Preview .dimension-dropdown-title i {
  font-size: 12px;
  margin-left: 8px;
}

.Preview .dropdown-menu {
  display: none;
  background: #fff;
  margin-top: 3px;
  border-radius: 3px;
  box-shadow: 0px 1px 3px -1px #888;
  padding: 4px 0px;
}

.Preview .dropdown-menu-item {
  padding: 4px 8px;
  cursor: pointer;
  font-size: 12px;
  color: #333;
}

.Preview .dropdown-menu-item.selected {
  background: #eee;
  cursor: default;
}

.Preview .dropdown-menu-item.selected:hover {
  background: #eee;
  color: #333;
}

.Preview .dropdown-menu-item:hover {
  background: #007bff;
  color: #fff;
}

.Preview .dimension-menu-item {
  margin-bottom: 10px;
}

.Preview .dimension-menu-item h5 {
  margin-bottom: 4px;
}
